<template>
  
  <div>

    <div class="row of-visible">
      <div class="col-sm-6 pt-2">
        <h5 class="fw-none">
          选择菜品
        </h5>
      </div>
      <div class="col-sm-6 pt-1 text-right of-visible">

        <button class="btn btn-info btn-sm pb-0 float-right" @click="sortOption">
          <i class="fa fa-toggle-down " v-if="!isSort"></i>
          <i class="fa fa-toggle-up" v-else></i>
        </button>

        <button class="btn btn-light btn-sm pb-0 float-right mr-2" @click="selectOption">
          <i class="fa fa-plus" v-if="!isAll"></i>
          <i class="fa fa-remove" v-else></i>
        </button>

      </div>
    </div>

    <table class="table m-0">
      <tr class="text-left">
        <td width="10%">序号</td>
        <td width="15%">货号</td>
        <td width="30%">描述</td>
        <td width="25%">
          单价
          <help-icon :help_code="'prices_input'"></help-icon>
        </td>
        <td width="10%"></td>
        <td width="10%" class="text-center">选择</td>
      </tr>
    </table>

  </div>

</template>

<script>
import tool from '@/common/es/tool'

import HelpIcon from '@/components/Modal/Help/HelpIcon.vue'
export default {
  components: { HelpIcon },
  name: 'PricesPlusTop',
  props: {
    _freights: {
      type: Array,
    },
    _select: {
      type: Array
    }
  },
  data() {
    return {
      isSort: false,
      isAll: false
    }
  },
  methods: {
    sortOption() {
      this._freights.reverse()
      this.isSort = !this.isSort
      if (this.isSort) { tool.showToast('逆序排列') } else { tool.showToast('顺序排列') }
    },
    selectOption() {
      if (!this.isAll) {
        const none = this._freights.filter((e) => {
          return !tool.inList(e.id, this._select)
        })
        
        for (const i of none) {
          this._select.push(i.id)
        }
        this.isAll = true

        tool.showToast('全选')
      } else {
        this._select.splice(1, (this._select.length - 1))
        this.isAll = false

        tool.showToast('移除全选')
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


